/* Modern Premium Theme - J&J Limo Services */
:root {
  --bg: #f8fafc;
  --card: #ffffff;
  --muted: #64748b;
  --accent: #0f172a;
  --primary: #0b6cf2;
  --primary-dark: #0956c4;
  --cta: #0b6cf2;
  --glass: rgba(255, 255, 255, 0.8);
  --border: #e2e8f0;
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

/* Extra small devices tweaks */
@media (max-width: 420px) {
  /* slightly larger tap targets for nav */
  .nav-links a {
    font-size: 16px;
    padding: 14px 0;
  }

  /* logo scale on very small screens */
  .logo-mark {
    height: 40px;
  }

  /* tighten hero spacing and font sizes */
  .hero {
    padding: 28px 12px;
    min-height: 55vh;
  }

  .hero h1 {
    font-size: 22px;
  }

  .hero p {
    font-size: 14px;
  }

  /* About/text spacing */
  .about-text {
    padding: 16px 14px;
  }

  .about-features {
    grid-template-columns: 1fr;
    gap: 12px;
  }

  /* Wizard / summary compacting */
  .summary-inner {
    padding: 16px;
  }

  .summary-card {
    position: static;
    width: 100%;
  }

  /* CTA banner compact */
  .cta-banner {
    padding: 24px 16px;
    gap: 16px;
  }

  /* Make buttons stack and fill width */
  .cta-banner-buttons,
  .cta-buttons {
    flex-direction: column;
    width: 100%;
  }

  .cta-banner-btn,
  .cta-primary,
  .cta-secondary {
    width: 100%;
  }

  /* smaller map iframe on tiny devices */
  .map-container iframe {
    height: 260px;
  }

  /* ensure vehicle cards remain readable */
  .vehicle-card img {
    width: 80px;
    height: 50px;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* skip link for A11y */
.skip-link {
  position: absolute;
  left: -9999px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
  background: transparent;
  color: var(--primary);
  z-index: 9999;
  text-decoration: none;
}
.skip-link:focus {
  left: 12px;
  top: 12px;
  width: auto;
  height: auto;
  padding: 8px 12px;
  border-radius: 8px;
  background: white;
  box-shadow: var(--shadow-md);
}

/* small route loader style used during lazy loading of pages */
.route-loader {
  padding: 60px 0;
  text-align: center;
  color: var(--muted);
  font-weight: 600;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
  background: var(--bg);
  color: var(--accent);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

/* Typography - Titles and Headings */
h1, h2, h3, h4, h5, h6,
.hero h1,
.section-title,
.page-title,
.step-title,
.logo h1,
.showcase-card h3,
.service-card h3,
.vehicle-detail-header h2,
.vehicle-detail-section h3,
.info-card h3,
.footer-section h3,
.services-cta h2,
.cta-banner-content h2,
.contact-form-section h2,
.contact-form-section-centered h2,
.faq-item h3,
.testimonial-author,
.summary-inner h4,
.about-section h2,
.contact-page h2,
.vehicles-page h2,
.services-page h2,
.terms-page h2,
.modal-header h2,
.modal-content h2 {
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-weight: 700;
  letter-spacing: -0.02em;
}

/* Body text and paragraphs - Applied globally */
p,
body,
input,
select,
textarea,
button,
a,
.nav-links a,
.contact-info a,
.footer-section p,
.footer-section a,
.service-card p,
.showcase-card p,
.vehicle-detail-description,
.about-text p,
.testimonial-text,
.faq-item p,
.info-card p,
.step-subtitle,
.section-subtitle,
.page-subtitle,
.about-section p,
.contact-page p,
.vehicles-page p,
.services-page p,
.terms-page p,
.feature-item,
.ideal-for,
.label,
.summary-row,
.review-row,
.addon-name,
.vehicle-name,
.stat-label,
.stat-number,
.muted,
.book-vehicle-btn,
.view-details-btn,
.cta-primary,
.cta-secondary,
.view-all-services-btn,
.book-vehicle-btn,
.modal-body p,
.modal-content p,
.btn-confirm,
.form-group label,
.features-list li,
.service-card ul li,
.testimonial-stars,
.footer-bottom p,
.info-note,
.brand-note,
.total-box small,
.summary-row small,
.coordinates-display,
.suggestion-main,
.suggestion-details {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
}

body.menu-open {
  overflow: hidden;
}

.container {
  width: min(1200px, 100%);
  margin: 0 auto;
  padding: 0 clamp(16px, 4vw, 32px);
}

img,
svg,
video,
canvas {
  max-width: 100%;
  height: auto;
}

main {
  width: 100%;
  overflow-x: hidden;
}

/* --------------------------------------------------- */
/* HEADER */
/* --------------------------------------------------- */

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.header {
  background: var(--card);
  border-bottom: 1px solid var(--border);
  box-shadow: var(--shadow-sm);
  position: sticky;
  top: 0;
  /* Ensure header/nav always sit above booking map and other content */
  z-index: 1000;
}

.header-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  gap: 24px;
  flex-wrap: wrap;
}

.logo h1 {
  font-size: 24px;
  font-weight: 700;
  color: var(--accent);
  margin: 0;
  letter-spacing: -0.02em;
}

.logo {
  display: flex;
  align-items: center;
  gap: 12px;
}

.logo-mark {
  height: clamp(40px, 4.2vw, 56px);
  width: auto;
  display: block;
  flex-shrink: 0;
}

.logo-text {
  display: none;
}

.nav-links {
  display: flex;
  gap: 24px;
  align-items: center;
}

.menu-toggle {
  display: none;
  flex-direction: column;
  gap: 6px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 10px;
  margin-left: auto;
}

.menu-toggle span {
  width: 26px;
  height: 3px;
  background: var(--accent);
  border-radius: 999px;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.menu-toggle.active span:nth-child(1) {
  transform: translateY(9px) rotate(45deg);
}

.menu-toggle.active span:nth-child(2) {
  opacity: 0;
}

.menu-toggle.active span:nth-child(3) {
  transform: translateY(-9px) rotate(-45deg);
}

.mobile-logo {
  display: none;
}

.mobile-close {
  display: none;
}

.mobile-contact {
  display: none;
  margin-top: 16px;
}

.mobile-contact a {
  color: var(--primary);
  font-weight: 600;
  text-decoration: none;
}

.nav-links a {
  color: var(--muted);
  text-decoration: none;
  font-size: 15px;
  font-weight: 500;
  transition: color 0.2s ease, border-color 0.2s ease;
  border-bottom: 2px solid transparent;
}

.nav-links a:hover {
  color: var(--primary);
}

.nav-links a.active {
  color: var(--primary);
  border-bottom-color: var(--primary);
}

.contact-info {
  display: flex;
  gap: 20px;
  align-items: center;
  flex-wrap: wrap;
}

.contact-info a {
  color: var(--primary);
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  transition: opacity 0.2s ease;
}

.contact-info a:hover {
  opacity: 0.8;
}

/* --------------------------------------------------- */
/* HERO SECTION */
/* --------------------------------------------------- */

.hero {
  position: relative;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: clamp(48px, 10vw, 120px) clamp(20px, 6vw, 48px);
  text-align: left;
  overflow: hidden;
  /* Slightly smaller so the background image doesn't feel too tall/overflowing */
  min-height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero-image-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.hero-background-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 1;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.4) 0%, rgba(118, 75, 162, 0.4) 100%);
  z-index: 1;
}

.hero-inner {
  position: relative;
  z-index: 2;
}

.hero-inner {
  width: min(700px, 100%);
  margin: 0 auto;
  text-align: left;
}

.hero h1 {
  font-size: clamp(2.25rem, 6vw, 3.75rem);
  font-weight: 800;
  margin: 0 0 16px;
  letter-spacing: -0.03em;
  line-height: 1.2;
}

.hero p {
  font-size: clamp(1rem, 2vw, 1.125rem);
  margin: 0 0 32px;
  opacity: 0.95;
  line-height: 1.6;
}

.hero .cta {
  display: inline-block;
  background: white;
  color: #667eea;
  padding: 16px 32px;
  border-radius: 12px;
  border: none;
  font-size: 16px;
  font-weight: 600;
  font-family: 'Inter', sans-serif;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  box-shadow: var(--shadow-lg);
  text-decoration: none;
}

.hero .cta:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-xl);
}

/* --------------------------------------------------- */
/* WIZARD AREA */
/* --------------------------------------------------- */

.wizard-area {
  padding: 48px 24px;
  min-height: 60vh;
}

.wizard-area .container {
  display: flex;
  gap: 32px;
  align-items: flex-start;
  max-width: 1200px;
}

.wizard-card {
  flex: 1;
  min-width: 0;
}

/* --------------------------------------------------- */
/* PROGRESS BAR */
/* --------------------------------------------------- */

.progress-bar-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 32px;
  padding: 0 8px;
}

.progress-step {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  position: relative;
}

.progress-step:not(:last-child)::after {
  content: '';
  position: absolute;
  top: 20px;
  left: 60%;
  right: -40%;
  height: 2px;
  background: var(--border);
  z-index: 0;
}

.progress-step:not(:last-child) .bar.completed::after {
  content: '';
  position: absolute;
  top: 20px;
  left: 60%;
  right: -40%;
  height: 2px;
  background: var(--primary);
  z-index: 1;
}

.circle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: var(--card);
  border: 2px solid var(--border);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 14px;
  color: var(--muted);
  position: relative;
  z-index: 2;
  transition: all 0.3s ease;
}

.circle.active {
  background: var(--primary);
  border-color: var(--primary);
  color: white;
  box-shadow: 0 0 0 4px rgba(11, 108, 242, 0.1);
}

.circle.completed {
  background: var(--primary);
  border-color: var(--primary);
  color: white;
}

.step-label {
  margin-top: 8px;
  font-size: 12px;
  color: var(--muted);
  font-weight: 500;
  text-align: center;
}

.progress-step .circle.active ~ .step-label,
.progress-step .circle.completed ~ .step-label {
  color: var(--primary);
  font-weight: 600;
}

/* --------------------------------------------------- */
/* STEP CARDS */
/* --------------------------------------------------- */

.step-card {
  background: var(--card);
  border-radius: 16px;
  padding: 32px;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border);
}

.step-title {
  margin: 0 0 24px;
  font-size: 24px;
  font-weight: 700;
  color: var(--accent);
  letter-spacing: -0.02em;
}

.step-subtitle {
  margin: -16px 0 24px;
  font-size: 15px;
  color: var(--muted);
  font-weight: 400;
}

.label {
  display: block;
  margin: 16px 0 8px;
  color: var(--accent);
  font-size: 14px;
  font-weight: 600;
}

.label .required-asterisk {
  color: #ef4444;
  margin-left: 4px;
  font-weight: 700;
}

.label .required-asterisk.hidden {
  display: none;
}

input[type="text"],
input[type="date"],
input[type="time"],
input[type="number"],
select,
textarea {
  width: 100%;
  padding: 14px 16px;
  border-radius: 10px;
  border: 2px solid var(--border);
  background: var(--card);
  outline: none;
  font-size: 15px;
  font-family: inherit;
  color: var(--accent);
  transition: all 0.2s ease;
}

input:focus,
select:focus,
textarea:focus {
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(11, 108, 242, 0.1);
}

input::placeholder {
  color: #94a3b8;
}

.phone-input-wrapper {
  display: flex;
  align-items: center;
  gap: 8px;
}

.phone-prefix {
  padding: 10px 10px;
  background: #f8fafc;
  border: 2px solid var(--border);
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  color: var(--accent);
  white-space: nowrap;
}

.phone-input {
  flex: 1;
}

.input-hint {
  display: block;
  margin-top: 6px;
  font-size: 12px;
  color: var(--muted);
  font-style: italic;
}

/* --------------------------------------------------- */
/* AUTOCOMPLETE */
/* --------------------------------------------------- */

.autocomplete-wrapper {
  position: relative;
  margin-bottom: 16px;
}

.input-field {
  width: 100%;
  padding: 14px 16px;
  border-radius: 10px;
  border: 2px solid var(--border);
  background: var(--card);
  outline: none;
  font-size: 15px;
  font-family: inherit;
  color: var(--accent);
  transition: all 0.2s ease;
}

.input-field:focus {
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(11, 108, 242, 0.1);
}

.suggestions-box {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: var(--card);
  border: 2px solid var(--border);
  border-top: none;
  border-radius: 0 0 10px 10px;
  box-shadow: var(--shadow-lg);
  z-index: 10;
  max-height: 300px;
  overflow-y: auto;
  margin-top: 2px;
}

.suggestion-item {
  padding: 12px 16px;
  cursor: pointer;
  font-size: 14px;
  color: var(--accent);
  border-bottom: 1px solid var(--border);
  transition: background 0.2s ease;
}

.suggestion-item:last-child {
  border-bottom: none;
}

.suggestion-item:hover {
  background: #f8fafc;
}

.suggestion-main {
  font-weight: 600;
  color: var(--accent);
  margin-bottom: 4px;
}

.suggestion-details {
  font-size: 12px;
  color: var(--muted);
  margin-bottom: 4px;
}

.suggestion-coords {
  font-size: 11px;
  color: var(--muted);
  font-family: 'Courier New', monospace;
}

.autocomplete-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.location-pin-btn {
  padding: 6px 12px;
  font-size: 12px;
  background: #f1f5f9;
  border: 1px solid var(--border);
  border-radius: 6px;
  cursor: pointer;
  color: var(--accent);
  font-weight: 500;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 4px;
}

.location-pin-btn:hover:not(:disabled) {
  background: #e2e8f0;
  border-color: var(--primary);
  color: var(--primary);
}

.location-pin-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.coordinates-display {
  margin-top: 8px;
  padding: 8px 12px;
  background: #f8fafc;
  border-radius: 6px;
  border: 1px solid var(--border);
}

.coordinates-display small {
  color: var(--muted);
  font-size: 12px;
  font-family: 'Courier New', monospace;
}

.show-map-btn {
  width: 100%;
  margin-top: 16px;
  padding: 12px 20px;
  background: #f8fafc;
  border: 2px solid var(--border);
  border-radius: 10px;
  color: var(--accent);
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.show-map-btn:hover {
  background: #f1f5f9;
  border-color: var(--primary);
  color: var(--primary);
}

.btn-row {
  display: flex;
  gap: 12px;
  margin-top: 24px;
  justify-content: flex-end;
}

.next-btn,
.back-btn,
.confirm-btn {
  padding: 12px 24px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  font-size: 15px;
  font-family: 'Inter', sans-serif;
  transition: all 0.2s ease;
}

.next-btn {
  background: var(--primary);
  color: white;
  box-shadow: 0 4px 12px rgba(11, 108, 242, 0.3);
}

.next-btn:hover:not(:disabled) {
  background: var(--primary-dark);
  transform: translateY(-1px);
  box-shadow: 0 6px 16px rgba(11, 108, 242, 0.4);
}

.next-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.back-btn {
  background: #f1f5f9;
  color: var(--accent);
  border: 1px solid var(--border);
}

.back-btn:hover {
  background: #e2e8f0;
}

.confirm-btn {
  background: #10b981;
  color: white;
  padding: 14px 28px;
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.3);
}

.confirm-btn:hover {
  background: #059669;
  transform: translateY(-1px);
}

/* --------------------------------------------------- */
/* ADDONS/EXTRAS */
/* --------------------------------------------------- */

.addons-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 16px;
  margin-top: 16px;
}

.addon-card {
  background: var(--card);
  border-radius: 12px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 12px;
  cursor: pointer;
  border: 2px solid var(--border);
  transition: all 0.2s ease;
  min-height: 100px;
}

.addon-card:hover {
  border-color: var(--primary);
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}

.addon-card.selected {
  border-color: var(--primary);
  background: rgba(11, 108, 242, 0.05);
  box-shadow: 0 0 0 3px rgba(11, 108, 242, 0.1);
}

.addon-name {
  font-weight: 600;
  font-size: 15px;
  color: var(--accent);
}

.addon-price {
  font-weight: 700;
  font-size: 18px;
  color: var(--primary);
}

/* --------------------------------------------------- */
/* VEHICLE GRID */
/* --------------------------------------------------- */

.vehicle-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 16px;
  margin-top: 16px;
}

.vehicle-card {
  background: var(--card);
  border-radius: 12px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  border: 2px solid var(--border);
  transition: all 0.2s ease;
}

.vehicle-card:hover {
  border-color: var(--primary);
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}

.vehicle-card.selected {
  border-color: var(--primary);
  background: rgba(11, 108, 242, 0.05);
  box-shadow: 0 0 0 3px rgba(11, 108, 242, 0.1);
}

.vehicle-card img {
  /* Auto-fit thumbnails inside the booking wizard vehicle grid */
  width: 100%;
  max-width: 150px;
  height: auto;
  max-height: 150px;
  object-fit: contain;
  pointer-events: none;
}

.vehicle-card .vehicle-name {
  font-weight: 600;
  font-size: 14px;
  color: var(--accent);
  text-align: center;
}

.vehicle-card .price {
  font-weight: 700;
  font-size: 16px;
  color: var(--primary);
}

/* --------------------------------------------------- */
/* SUMMARY CARD */
/* --------------------------------------------------- */

.summary-card {
  width: 360px;
  min-width: 320px;
  position: sticky;
  top: 100px;
}

.summary-inner {
  background: var(--card);
  padding: 24px;
  border-radius: 16px;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border);
}

.summary-inner h4 {
  margin: 0 0 20px;
  font-size: 18px;
  font-weight: 700;
