/* --------------------------------------------------- */
/* BACK TO TOP BUTTON */
/* --------------------------------------------------- */

.back-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 56px;
  height: 56px;
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-dark) 100%);
  color: white;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 20px rgba(11, 108, 242, 0.4), 0 0 0 0 rgba(11, 108, 242, 0.5);
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px) scale(0.8);
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  z-index: 1000;
  overflow: hidden;
}

.back-to-top::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}

.back-to-top:hover::before {
  width: 300px;
  height: 300px;
}

.back-to-top.show {
  opacity: 1;
  visibility: visible;
  transform: translateY(0) scale(1);
  animation: pulse 2s infinite;
}

body.menu-open .back-to-top {
  display: none !important;
}

.back-to-top:hover {
  background: linear-gradient(135deg, var(--primary-dark) 0%, var(--primary) 100%);
  transform: translateY(-5px) scale(1.1);
  box-shadow: 0 8px 30px rgba(11, 108, 242, 0.6), 0 0 0 8px rgba(11, 108, 242, 0.1);
}

.back-to-top:active {
  transform: translateY(-2px) scale(1.05);
}

.back-to-top svg {
  position: relative;
  z-index: 1;
  transition: transform 0.3s ease;
}

.back-to-top:hover svg {
  transform: translateY(-2px);
}

@keyframes pulse {
  0%, 100% {
    box-shadow: 0 4px 20px rgba(11, 108, 242, 0.4), 0 0 0 0 rgba(11, 108, 242, 0.5);
  }
  50% {
    box-shadow: 0 4px 20px rgba(11, 108, 242, 0.4), 0 0 0 10px rgba(11, 108, 242, 0);
  }
}

